import Vue from 'vue'
import Router from 'vue-router'

import index from '@/views/Index.vue'
import fellowships from '@/views/Fellowships.vue'
import publications from '@/views/Publications.vue'
import presentations from '@/views/Presentations.vue'
import teachings from '@/views/Teachings.vue'
import links from '@/views/Links.vue'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'home',
            component: index
        },
        {
            path: '/fellowships',
            name: 'fellowships',
            component: fellowships
        },
        {
            path: '/publications',
            name: 'publications',
            component: publications
        },
        {
            path: '/presentations',
            name: 'presentations',
            component: presentations
        },
        {
            path: '/teachings',
            name: 'teachings',
            component: teachings
        },
        {
            path: '/links',
            name: 'links',
            component: links
        }
    ]
})